.header-search-box {
  width: 150px;
  height: 38px;
  background: rgba(191, 191, 191, 0.28);
  border-radius: 19px;
  border-style: none;
  outline: none;
  padding-left: 20px;
  padding-right: 40px;
  font-size: 16px;
  color: rgba(75, 75, 75, 0.8);
}
input::-webkit-input-placeholder,
input::-moz-placeholder,
input::-ms-input-placeholder,
textarea::-webkit-input-placeholder,
textarea::-moz-placeholder,
textarea::-ms-input-placeholder {
  font-size: 16px;
  line-height: 27px;
  color: rgba(75, 75, 75, 0.8);
  /* opacity: 1; */
}
.header-search-icon {
  position: absolute;
  top: 10px;
  right: -6px;
  font-size: 22px;
  display: inline-block;
  color: rgba(75, 75, 75, 0.6);
  cursor: pointer;
  margin-right: 10px;
}
.header-search-text {
  position: absolute;
  top: 11px;
  right: -16px;
  font-size: 16px;
  display: inline-block;
  width: 50px;
  color: rgba(75, 75, 75, 1);
  cursor: pointer;
}
.search-text {
  font-size: 17px;
  position: relative;
  right: 180px;
  bottom: 4px;
}
